"use client";

import { Card } from "@/components/ui/card";
import Sidebar from "@/components/Dashboard/Sidebar";

export default function DashboardPage() {
  // Mock data - in a real app, this would come from an API
  const accountData = {
    balance: 5000000,
    savings: 3000000,
    loan: 2000000,
    transactions: [
      {
        id: 1,
        type: "deposit",
        amount: 1000000,
        date: "2024-02-20",
        status: "success",
      },
      {
        id: 2,
        type: "withdrawal",
        amount: 500000,
        date: "2024-02-19",
        status: "success",
      },
      {
        id: 3,
        type: "loan_payment",
        amount: 750000,
        date: "2024-02-18",
        status: "success",
      },
    ],
  };

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat("id-ID", {
      style: "currency",
      currency: "IDR",
      minimumFractionDigits: 0,
    }).format(amount);
  };

  return (
    <div className="min-h-screen bg-gray-100">
      <Sidebar />
      
      <main className="ml-64 p-8">
        <div className="max-w-7xl mx-auto space-y-6">
          <h1 className="text-2xl font-bold text-gray-900">Dashboard</h1>

          {/* Account Summary Cards */}
          <div className="grid md:grid-cols-3 gap-6">
            <Card className="p-6 space-y-2">
              <h3 className="text-sm font-medium text-gray-500">Total Saldo</h3>
              <p className="text-2xl font-bold text-gray-900">
                {formatCurrency(accountData.balance)}
              </p>
            </Card>

            <Card className="p-6 space-y-2">
              <h3 className="text-sm font-medium text-gray-500">Total Simpanan</h3>
              <p className="text-2xl font-bold text-green-600">
                {formatCurrency(accountData.savings)}
              </p>
            </Card>

            <Card className="p-6 space-y-2">
              <h3 className="text-sm font-medium text-gray-500">Total Pinjaman</h3>
              <p className="text-2xl font-bold text-blue-600">
                {formatCurrency(accountData.loan)}
              </p>
            </Card>
          </div>

          {/* Recent Transactions */}
          <Card className="p-6">
            <h2 className="text-lg font-semibold text-gray-900 mb-4">
              Transaksi Terakhir
            </h2>
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="text-left border-b border-gray-200">
                    <th className="pb-2 font-medium text-gray-500">Tanggal</th>
                    <th className="pb-2 font-medium text-gray-500">Tipe</th>
                    <th className="pb-2 font-medium text-gray-500">Jumlah</th>
                    <th className="pb-2 font-medium text-gray-500">Status</th>
                  </tr>
                </thead>
                <tbody>
                  {accountData.transactions.map((transaction) => (
                    <tr key={transaction.id} className="border-b border-gray-100">
                      <td className="py-3">{transaction.date}</td>
                      <td className="py-3">
                        {transaction.type === "deposit"
                          ? "Setoran"
                          : transaction.type === "withdrawal"
                          ? "Penarikan"
                          : "Pembayaran Pinjaman"}
                      </td>
                      <td className="py-3">{formatCurrency(transaction.amount)}</td>
                      <td className="py-3">
                        <span className="px-2 py-1 text-sm rounded-full bg-green-100 text-green-800">
                          {transaction.status === "success" ? "Berhasil" : "Gagal"}
                        </span>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </Card>
        </div>
      </main>
    </div>
  );
}
